<template>
  <div class="box">
    <!-- <router-link to="/mymsg">my</router-link> -->
    <!-- 头部 -->
    <div class="my_header" v-if="isLogin">
      <div class="my_header_top">
        <div class="my_header_img">
          <van-image round width="70px" height="70px" :src="userval.avatar" />
          <span>{{userval.nickname}}</span>
        </div>
        <div >
          <van-button round type="info" class="btn" @click="bianji(userval)">编辑资料</van-button>
        </div>
      </div>
      <div class="my_header_bottom">
          <div class="border" @click="userfabu">
             <p>{{usermsg.publish_num}}</p>
             <p>发布</p>
          </div>
          <div>
            <p>{{usermsg.liked_num}}</p>
            <p>获赞</p>
          </div>
      </div>
    </div>
    <div v-else>
        <div class="beijin">
           <h3>登录后你能看的更多</h3>
           <van-button type="default" class="btn" to="/login" >登录</van-button>
        </div>
    </div>
    <div class="my_content">
          <div class="liulanlishi" @click="shoucang">
             <van-icon name="star-o" size="30" color="#f00"/>
             <p>收藏历史</p>
          </div>
          <div class="liulanlishi" @click="browsingHistory">
            <van-icon name="underway-o" size="30" color="rgb(236, 110, 19)"/>
            <p>浏览历史</p>
          </div>
    </div>
    <div class="my_content_neirong">
      <div @click="updatemima">
         <van-cell title="修改密码" is-link class="border" />
      </div>
      <van-cell title="联系我们" is-link class="border" @click="relationWe"/>
      <van-cell title="关于我们" is-link class="border" @click="aboutWe"/>
    </div>
    
    <van-button type="default" block class="btn_footer" @click="backLogin">退出登录</van-button>
    
  </div>
</template>

<script>
import { Icon, Image as VanImage, Button, Cell, CellGroup,Toast,Dialog  } from "vant";
export default {
  name:'my',
  components: {
    [Icon.name]: Icon,
    [VanImage.name]: VanImage,
    [Button.name]: Button,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup,
    [Toast.name]:Toast ,
    [Dialog.name]:Dialog

  },
  data() {
    return {
      isLogin:false,
      token:'',
      uid:'',
      usermsg:'',
      userval:'',
      tokenExpired:''
    }
  },
  methods:{
    login(){
     this.token =localStorage.getItem('token')
     this.uid =localStorage.getItem('uid')
     this.tokenExpired=localStorage.getItem('tokenExpired')
    //  console.log(this.tokenExpired);
     if(this.token&&this.uid){
        let now = +new Date() ;
         if(now>=this.tokenExpired){
            Toast('你的账户长时间没登陆，请重新登录')  
         }else{
          this.isLogin=true
         }
     }
    },
    backLogin(){
      
      Dialog.confirm({
          title: '确认退出吗',
        })
          .then(() => {
            // 删除指定的一个
           localStorage.removeItem('token')
           localStorage.removeItem('uid')
           localStorage.removeItem('tokenExpired')
           localStorage.removeItem('userInfo')
            // 删除所有
            //localStorage.clear()
           this.isLogin=false
          })
          .catch(() => {
            // on cancel
      });
     
      // console.log(this.uid);
    },
    usermsgInfo(){
        this.$axios({
          method:'post',
          url:'https://d33a5037-6c63-4e92-8bb1-30018ab701ea.bspapp.com/http/user/getuserInfo',
          data:{
            'token':this.token
          }
        }).then(res=>{
           this.usermsg=res.data
           this.userval=res.data.userInfo
          console.log('user',this.usermsg);
        })
    },
    bianji(userval){
      // console.log('1',userval);
      this.$router.push({
        name:'mymsg',
        params:{
          userval:userval
          }
      })
    },
    userfabu(){
        this.$router.push('/mypublic')
    },
    updatemima(){
    if(this.token&&this.uid){
        let now = +new Date() ;
         if(now>=this.tokenExpired){
            Toast('你的账户长时间没登陆，请重新登录')  
         }else{
           this.$router.push({
            name:'mychangemima'
          })
         }
        }else{
          Toast('你的账户还没登陆，请登录')
        }
  },
  shoucang(){
    if(this.token&&this.uid){
        let now = +new Date() ;
         if(now>=this.tokenExpired){
            Toast('你的账户长时间没登陆，请重新登录')  
         }else{
           this.$router.push({
            name:'myshoucang'
          })
         }
        }else{
          Toast('你的账户还没登陆，请登录')
        }
  },
  relationWe(){
    Toast('开发中！！！')
  },
  aboutWe(){
    Toast('开发中！！！')
  },
  browsingHistory(){
    Toast('开发中！！！')
  }
  },
  created(){
    this.login()
    this.usermsgInfo()
  },
  
  activated(){
    // this.usermsgInfo()
  }
};
</script>

<style lang="less" scoped>
.beijin{
  // width: 100vw;
  height: 180px;
  // background-size:100%;
  // background-image:url(@/assets/shibai.png);
  background-color: #c3bfbf;
  text-align: center;
  padding-top: 100px;
  .btn{
    width: 120px;
    margin-top: 30px;
  }
}
.box{
  height: 100vh;
  width: 100vw;
  background-color: rgb(243, 243, 243);
}
.my_header {
  background-color: rgb(16, 173, 81);
  height: 180px;
  .my_header_top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:30px 10px 5px 10px;
    .my_header_img{
      display: flex;
      justify-content: space-around;
      align-items: center;
      span{
        margin-left: 10px;
        color: #fff;
      }
    }
    .btn{
      height: 30px;
      background-color: #fff;
      color: #000;
      
    }
  }
  .my_header_bottom{
    display: flex;
    justify-content:space-around;
    align-items: center;
    margin-top: 20px;
    p{
      color: #fff;
      text-align: center;
    }
  }
}
.my_content{
    height: 80px;
    display: flex;
    justify-content:space-around;
    background-color: #fff;
    align-items: center;
    p{
      color: rgb(0, 0, 0);
      text-align: center;
      margin-top: 4px;
    }
}
.my_content_neirong{
   margin-top: 5px;
   .border{
    border-bottom: 1px solid rgb(243, 243, 243);
   }
}
.btn_footer{
  color: rgb(255, 0, 0);
  background-color: #fff;
  margin-top: 6px;
}
.liulanlishi{
  text-align: center;
}
</style>